html,
body {
  margin: 0;
  padding: 0; }

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400; }

@media screen and (max-width: 400px) {
  .hide-on-mobile {
    display: none; } }

.app .app-body {
  padding-top: 64px; }

@media screen and (max-width: 599px) {
  .app .app-body {
    padding-top: 56px; } }



